import React from "react"

function Hero() {
 return(
    <section id="hero">
    <img src="images/bgVirus.png" alt="virus-icon" class="virus-right"/>
    <div class="container-fluid">
      <div class="row">
        <div class="col-lg-6 ">
          <h1 class="hero-heading">Stay Safe from <br /> Corona virus</h1>
          <p class="hero-text">Coronavirus disease (COVID-19) is an infectious disease caused by a new virus. The disease causes respiratory illness (like the flu) with symptoms such as a cough, fever, and in more severe cases, difficulty breathing.</p>
          <a href="" class="btn btn-danger">Get Latest News</a>
        </div>
        <div class="col-lg-6">
          <img src="images/virus.png" alt="virus-icon" class="mini-virus-left img-fluid"/>
          <img src="images/virus.png" alt="virus-icon" class="mini-virus-middle img-fluid"/>
          <img src="images/virus.png" alt="virus-icon" class="mini-virus-right img-fluid"/>
          <img src="images/hero.png" alt="sick-man-icon" class="hero img-fluid"/>
        </div>
      </div>
    </div>
  </section>

 )
}

export default Hero;