import React from "react"

function Symptoms() {
    return(
<section id="symptoms">
    <div class="container-fluid">
      <div class="row">
        <div class="col-lg-4 col-xs-6">
          <img src="images/doctor-woman.svg" alt="woman-doctor" class="img-fluid"/>
        </div>
        <div class="col-lg-8 col-xs-6">
          <h1 class="symptoms-heading">What are the symptoms? </h1>
          <p class="symptoms-text">COVID-19 symptoms range from mild to severe. It takes 2-14 days after exposure for symptoms to develop. Symptoms may include:</p>
          <img src="images/symptoms.png" alt="sick-woman" class="sick-woman img-fluid"/>
        </div>
      </div>
    </div>
  </section>

    )
}
export default Symptoms;