import React from "react"

function Tracker(props) {
    return(
        <section id="tracker">
        <div class="container-fluid">
          <h1 class="tracker-overview">Case Overview Today</h1>
          <hr/>
          <div class="row">
            <div class="col-lg-4">
              <div class="tracker-card total-case">
                <h1 class="tracker-numbers counter ">{props.totalCases}</h1>
                <p class="tracker-text">Total Cases</p>
              </div>
            </div>
            <div class="col-lg-4">
              <div class="tracker-card today-case">
                <h1 class="tracker-numbers">{props.todayCases}</h1>
                <p class="tracker-text">Today Cases</p>
              </div>
            </div>
            <div class="col-lg-4">
              <div class="tracker-card deaths">
                <h1 class="tracker-numbers">{props.deaths}</h1>
                <p class="tracker-text">Deaths</p>
              </div>
            </div>
            <div class="col-lg-4">
              <div class="tracker-card today-deaths">
                <h1 class="tracker-numbers">{props.todayDeaths}</h1>
                <p class="tracker-text">Today Deaths</p>
              </div>
            </div>
            <div class="col-lg-4">
              <div class="tracker-card recovered">
                <h1 class="tracker-numbers">{props.recovered}</h1>
                <p class="tracker-text">Recovered</p>
              </div>
            </div>
            <div class="col-lg-4">
              <div class="tracker-card active-cases">
                <h1 class="tracker-numbers">{props.activeCases}</h1>
                <p class="tracker-text">Active Cases</p>
              </div>
            </div>
          </div>
          <p>Data source from github api <a href="#">Click Here</a></p>
        </div>
      </section>

    )
}

export default Tracker;