import React, { Component } from 'react';
import './App.css';
import Navigation from "./components/navigation/navigation"
import Hero from "./components/hero/hero"
import Tracker from "./components/tracker/tracker"
import Symptoms from "./components/symptoms/symptoms"
import News from "./components/news/news"
import Footer from './components/footer/footer';


class App extends Component {

  constructor() {
    super();
    this.state = {
      covid: [],
      news: []
    };
  }

  // Covid API
  componentDidMount() {
    fetch("https://coronavirus-19-api.herokuapp.com/countries/philippines")
    .then((resp) => resp.json())
    .then((data) => this.setState({covid: data}))
  // News API

  fetch("https://newsapi.org/v2/top-headlines?q=covid&country=ph&apiKey=4df115dfb7bc47759730b8bc7e865a32")
  .then((resp) => resp.json())
  .then((data) => this.setState({news: data}))


  }


  render() {
    const {covid, news} = this.state
const Allnews = news.articles

    return(
    <div className="App">
    <Navigation />

    <Hero />
    <Tracker 
      totalCases={covid.cases}
      todayCases={covid.todayCases}
      deaths={covid.deaths}
      todayDeaths={covid.todayDeaths}
      recovered={covid.recovered}
      activeCases={covid.active}
    />
    <Symptoms />
    <News
    data={Allnews}
     />
    <Footer/>
    </div>
    )
  }
}


export default App;
